import { createStore } from "vuex";
import axios from "axios";

export default createStore({
  state: {
    apiKey: "695ca0d88a614aff1fec87d43bceb794",
    getFullWeather: null,
  },
  mutations: {
    getweather(state, payload) {
      state.getFullWeather = payload;
    },
  },
  actions: {
    async getWeather({ commit, state }, city = "Toshkent") {
      try {
        const res = await axios.get(
          `https://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKey}`
        );
        let weather = res.data[0];
        let { lat, lon, name } = weather;
        const fullRes = await axios.get(
          `https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=hourly,alerts,minutely&appid=${state.apiKey}&units=metric&lang=ru`
        );
        let weatherInfo = fullRes.data;
        let weatherObj = { ...weatherInfo, name };
        console.log(weatherObj);
        commit("getweather", weatherObj);
      } catch (error) {
        console.log(error);
      }
    },
  },
  getters: {
    getFullWeather: (state) => state.getFullWeather,
    getDailyWeather: (state) =>
      state.getFullWeather.daily.filter((day, i) => i != 0),
  },
  modules: {},
});
